<template>
<div>
<swiper :key="looplist.length" ref="myswiper">
    <div class="swiper-slide" v-for="data in looplist" :key="data.bannerId">
        <img :src="data.imgUrl"/>
    </div>
</swiper>
<!--<img src="../assets/logo.png" />
<img src="./Film/logo.png" />-->
<filmheader :class="isFixed?'fixed':''"></filmheader>
<router-view></router-view>
</div>
</template>
<script>
import filmheader from '@/views/Film/FilmHeader'
import swiper from '@/views/Film/Swiper'

import axios from 'axios'
export default {
  data () {
    return {
      looplist: [],
      isFixed: false
    }
  },
  components: {
    filmheader,
    swiper
  },
  mounted () {
    axios.get('/tplb.json').then(res => {
      console.log(res)
      console.log(res.data.length)
      this.looplist = res.data
    }).catch(err => {
      console.log(err)
    })

    window.onscroll = this.handleScroll
  },
  beforeUnmount () {
    // console.log("beforeDestroy")
    window.onscroll = null
  },
  methods: {
    handleScroll () {
      // console.log("1111")
      // console.log(document.documentElement.scrollTop,this.$refs.myswiper.$el.offsetHeight)
      if (document.documentElement.scrollTop >= this.$refs.myswiper.$el.offsetHeight) {
        // console.log("fixed")
        this.isFixed = true
      } else {
        // console.log("unfixed")
        this.isFixed = false
      }
    }
  }
}
</script>
