<template>
<div>
detaill
<div v-if="filminfo">
{{filminfo.name}}<br/>
</div>
</div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      filminfo: null
    }
  },
  props: ['id'],
  mounted () {
    // console.log(this.$route.params.id,this.id)
    this.$store.state.isTabbarShow = false
    axios({
      url: `https://m.maizuo.com/gateway?filmId=${this.id}&k=5986680`,
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16048937651304037970411521"}',
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then(res => {
      console.log(res.data.data)
      // this.filminfo=res.data
    })
    // axios.post(`http://crm.bravergo.com/api/wechart/getWechart`,{cid:1274}).then(res=>{
    // console.log(res.data)
    // })
    axios.get('/detail.json').then(res => {
      console.log(res.data.data.film)
      this.filminfo = res.data.data.film
    })
  },
  beforeMount () {
    this.$store.commit('hidemaizuotabbar', false)
  },
  unmounted () {
    this.$store.state.isTabbarShow = true
    this.$store.commit('showmaizuotabbar', true)
  }
}
</script>
