<template>
<div>
Nowplaying
<ul>
<li v-for="data in datalist" :key="data.filmId" @click="handleChangePage(data.filmId)" style="display:block;clear:both;padding: 15px 15px 15px 0;">
<img :src="data.poster" style="width:128px;height:180px"/>
<h3 style="text-align:left;padding-left:10px">{{data.name}}</h3>
<p style="text-align:left;padding-left:10px">观众评分{{data.grade}}</p>
<!--<p>主演：{{data.actors}}</p>-->
<p style="text-align:left;padding-left:10px">主演：<span v-for="(num,key) in data.actors" :key="key">&nbsp;{{num.name}} </span></p>
</li>
</ul>
</div>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      datalist: []
    }
  },
  mounted () {
    // axios.get("https://m.maizuo.com/gateway?cityId=310100&pageNum=1&pageSize=10&type=1&k=6477869").then(res=>{
    //    console.log(res.data);
    // })
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=310100&pageNum=1&pageSize=10&type=1&k=6477869',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16048937651304037970411521","bc":"310100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      // console.log(res.data)
      this.datalist = res.data.data.films
    })
  },
  methods: {
    handleChangePage (id) {
      // console.log(id)
      // 编程式导航-路径跳转
      // this.$router.push(`/detail/${id}`)

      // 编程式导航-名字跳转
      this.$router.push({ name: 'detail', params: { id: id } })
    }
  }
}
</script>
<style scoped>
ul{
    clear:both;
    widht:100%;
    overflow-x:hidden
}
li img{
    float:left;
    list-style:none
}
</style>
