<template>
<div class="cinema" :style="mystyle">
<ul>
    <li v-for="data in datalist" :key="data.cinemaId">{{data.name}}</li>
</ul>
</div>
</template>
<script>
import axios from 'axios'
import BetterScroll from 'better-scroll'
export default {
  data () {
    return {
      datalist: [],
      mystyle: {
        height: document.documentElement.clientHeight - 50 + 'px'
      }
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=110100&ticketFlag=1&k=1337952',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16048937651304037970411521","bc":"110100"}',
        'X-Host': 'mall.film-ticket.cinema.list'
      }
    }).then(res => {
      // console.log(res)
      this.datalist = res.data.data.cinemas

      this.$nextTick(() => {
        this.scroll = new BetterScroll('.cinema', {
          scrollbar: {
            fade: true,
            interactive: false
          }
        })
      })
    })
    console.log(document.documentElement.clientHeight)
  }
}
</script>
<style scoped>
.cinema{
    height:500px;
    overflow:hidden;
    position:relative
}
ul li{
    list-style:none;
    height:50px;
    line-height:50px
}
</style>
