<template>
<div>
<ul>
<li v-for="data in $store.state.comingList" :key="data.filmId">
<img :src="data.poster" />
<h3>{{data.name}}</h3>
<p>观众评分{{data.grade}}</p>
<p>主演：{{data.actors}}</p>
</li>
</ul>
</div>
</template>
<script>
export default {
  mounted () {
    if (this.$store.state.comingList.length === 0) {
      // ajax请求
      this.$store.dispatch('getComingListAction')
    } else {
      console.log('使用缓存数据')
    }
  }
}
</script>
<style scoped>
ul{
    clear:both;
    widht:100%;
    overflow-x:hidden
}
li img{
    float:left;
    list-style:none
}
</style>
