import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    // 自定义的共享
    isTabbarShow: true,
    comingList: []
  },
  mutations: {
    hidemaizuotabbar (state, data) {
      // console.log("hidemaizuotabbar");
      state.isTabbarShow = data
    },
    showmaizuotabbar (state, data) {
      state.isTabbarShow = data
    },
    comingListMutation (state, data) {
      state.comingList = data
    }
  },
  actions: {
    // 异步处理
    getComingListAction (store) {
      axios({
        url: 'https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=2&k=5291327',
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16048937651304037970411521","bc":"110100"}',
          'X-Host': ' mall.film-ticket.film.list'
        }
      }).then(res => {
        store.commit('comingListMutation', res.data.data.films)
      })
    }
  },
  modules: {
  }
})
